import qs from 'qs';

export default {
  props: {
    _export: {
      /*
        对象格式为：{
          url: '',
          text: '',
          ...export组件支持的属性
        }
      */
      type: [Boolean, Object],
      default: false,
    },
  },
  data() {
    return {
      exportStr: '',
    };
  },
  computed: {
    exportUrl() {
      if (this.type === 'front') return '';
      const url = (this._export && this._export.url) || this.url || '';
      const backExportUrl = `${url}?${qs.stringify({ ...this.formParams, ...this.params, action: 'export' })}`;
      return backExportUrl;
    },
  },
  methods: {
    getExportData() {
      // 普通表头数组
      const keys = [];
      // 固定表头数组
      const fixKeys = [];
      const getHeader = (columns = this.currentColumns) => {
        const titles = [];
        const fixTitles = [];

        const helper = (arr, prevItems = []) => {
          // eslint-disable-next-line no-restricted-syntax
          for (const item of arr) {
            if (item.children && item.children.length) {
              helper(item.children, [...prevItems, item]);
            } else {
              const title = [...prevItems, item].map(_item => _item.title).join('-');
              const targetTitles = item.fixed ? fixTitles : titles;
              const targetKeys = item.fixed ? fixKeys : keys;
              targetTitles.push(title);
              targetKeys.push(item.key);
            }
          }
        };

        helper(columns);

        const titlesStr = `${[...fixTitles, ...titles].join()}\n`;
        return titlesStr;
      };
      const getRows = (rows = this.initRows) => {
        const rowsArr = [];
        // eslint-disable-next-line no-restricted-syntax
        for (const item of rows) {
          const rowArr = [];
          // eslint-disable-next-line no-restricted-syntax
          for (const key of [...fixKeys, ...keys]) {
            const val = item[key] === '0' ? '0' : item[key] || ''; // 兼容0的判断
            const objVal = val && val.title && val.title === '0' ? '0' : (val && val.title) || ''; // 兼容0的判断
            let resVal = objVal || val;
            if (+resVal && resVal.length > 11) resVal = `\t${resVal}`; // 超过11位的数字不显示计数法
            resVal = `${resVal}`.replace(/,/g, '-'); // ','是csv格式，需修改为'-'

            rowArr.push(resVal);
          }
          rowsArr.push(`${rowArr.join()}\n`);
        }
        const rowsStr = rowsArr.join('');
        return rowsStr;
      };
      const getFooter = (footer = this.footer) => {
        const footerArr = Object.values(footer);
        const footerStr = footerArr ? `${footerArr.join()}\n` : '';
        return footerStr;
      };

      return getHeader() + getRows() + getFooter();
    },
  },
};
